.configuration {
  &__wrapper {
    min-width: 320px;
    width: 100%;
    min-height: 420px;
    box-sizing: border-box;
    border: 1px solid #eee;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__field {
    margin: 10px 10px;
    margin-bottom: 0;
  }

  &__thumbs-current-value-list {
    width: 35%;
    margin: 0;
    padding-left: 30px;
  }

  &__thumbs-interval-value-list {
    width: 65%;
    margin: 0;
    padding-left: 20px;
  }

  &__update {
    visibility: hidden;
  }

  &__thumbs-value {
    width: 85%;
    color: $Nero;
    border: 1px solid #eee;
    border-radius: 2px;
    vertical-align: baseline;
    outline: none;
    font-family: "Fire Sans", Arial, sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    margin-top: 5px;
    padding-left: 5px;

    &:focus {
      border-color: $PaleCornflowerBlue;
    }
  }

  &__thumbs-set {
    display: flex;
    justify-content: space-between;
  }

  &__thumbs-value-from {
    width: 40%;
    color: $Nero;
    border: 1px solid #eee;
    border-radius: 2px;
    vertical-align: baseline;
    outline: none;
    font-family: "Fire Sans", Arial, sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    margin-top: 5px;
    padding-left: 5px;

    &:focus {
      border-color: $PaleCornflowerBlue;
    }
  }

  &__thumbs-value-to {
    width: 40%;
    color: $Nero;
    border: 1px solid #eee;
    border-radius: 2px;
    vertical-align: baseline;
    outline: none;
    font-family: "Fire Sans", Arial, sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    margin-top: 5px;
    padding-left: 5px;

    &:focus {
      border-color: $PaleCornflowerBlue;
    }
  }
}